@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@300;500;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root {
  width: 100%;
  height: 100vh;
  /* background-color: blue; */
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Raleway", sans-serif;
}

#quote-box {
  padding: 40px 50px;
  width: 550px;
  background-color: white;
}

#quote-text {
  font-size: 28px;
  font-weight: 500;
  text-align-last: center;
}

#quote-text i {
  margin-right: 10px;
}

#quote-author {
  padding-top: 20px;
}

#author {
  display: block;
  text-align: end;
  font-size: 16px;
  text-transform: capitalize;
}

#quote-btns > a {
  color: #ffffff;
  font-size: 20px;
  padding: 8px;
  background-color: #ff0000;
  border: none;
  box-shadow: none;
  outline: none;
}

#quote-btns {
  margin-top: 30px;
  color: black;
  display: flex;
  justify-content: space-between;
}

#quote-btns button {
  padding: 8px 18px;
  color: white;
  border: none;
}

#quote-btns button:focus {
  /* border: none; */
  outline: none;
}

.footer {
  padding: 15px 50px 30px;
  text-align: center;
  font-size: 15px;
  color: white;
  letter-spacing: 5px;
}
